h3 {
    color: white;
}

a[href*='github.com'] {
    position: absolute;
    top: 1em;
    right: 1em;
}

a[href*='github.com']:before {
    content: '';
    width: 1.1em;
    height: 1.1em;
    display: inline-block;
    position: relative;
    top: 4px;
    background: url('https://vinkcode.github.io/images/Octocat.png');
    background-size: contain;
    background-repeat: no-repeat;
    border-radius: 4px;
}


/*tmp*/

img {
    display: none;
}

a {
    color: #063754;
    text-decoration: none;
    text-shadow: none;
}

a:hover {
    color: #063754;
    text-decoration: underline;
}

section {
    position: relative;
    color: white;
    background: #708090;
    margin: 1em;
    padding: 1em;
    text-shadow: rgb(0, 0, 0) 1px 1px;
}

section .col {
    display: flex;
    flex-direction: row
}

section .col>div {
    flex-grow: 1;
    flex-basis: 0
}

section .col>div>a button {
    position: absolute;
    right: 1em;
    bottom: 1em;
    padding: 1em;
    border-radius: 4px;
    color: white;
    font-weight: bold;
    background: rgb(148, 17, 83);
    border: 0;
    border-radius: 0;
    text-shadow: 1px 1px black;
}